.login{
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: #00000090;
    display: grid;
}
.logincont{
    place-self: center;
    width: max(23vw, 330px);
    color: #808080;
    background-color: white;
    display: flex;
    flex-direction: column;
    gap: 25px;
    padding: 25px 30px;
    border-radius: 8px;
    font-style: 14px;
    animation: fadeIn .5s;
}
.logintitle{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: black;
}
.logintitle img{
    width: 16px;
    cursor: pointer;
}
.login-input{
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.login-input input{
    outline: none;
    border: 1px solid #c9c9c9;
    padding: 10px;
    border-radius: 4px;
}
.logincont button{
    border: none;
    padding: 10px ;
    border-radius: 4px ;
    color: white;
    background-color: tomato;
    font-size: 15px;
    cursor: pointer;
}
.logincondition{
    display: flex;
    align-items: start;
    gap: 8px;
    margin-top: -15px;
}
.logincondition input{
    margin-top: 5px;
}
.login{
    color: tomato;
    font-weight: 500;
    cursor: pointer;
}