.explore{
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.explore h1{
    color: #262626;
    font-weight: 500;
}
.explore-text {
    max-width: 60%;
    color: #808080;
}
.explore-list{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    text-align: center;
    margin: 20px 0px ;
    overflow-x: scroll;
}
.explore-list::-webkit-scrollbar {
    display: none;
}
.explore-list-item img{
    width: 7.5vw;
    min-width: 80px;
    cursor: pointer;
    border-radius: 50%;
    transition: 02s;
}
.explore-list-item p{
  margin-top: 10px;
  color: #747474;
  font-size: max(1.4vw, 16px);
  cursor: pointer;
}
.explore hr{
margin: 10px 0;
height: 2px ;
background-color: #e2e2e2;
border: none;
}
.explore-list-item .active{
    border: 4px solid tomato;
    padding: 2px ;
}
@media(max-width:1050px){
    .explore-text{
        max-width: 100%;
        font-size: 14px;
    }
}