.cart{
    margin-top: 100px;
}
.cart-title{
    display: grid;
    grid-template-columns: 1fr 1.5fr 1fr 1fr 1fr 0.5fr;
    align-items: center;
    color: grey;
    font-size: max(1vw, 12px);
}
.cartitems{
    margin: 10px 0px;
    color: black;

}
.cartitems img{
    width: 50px;
}
.cart hr{
    height: 1px;
    background-color: #e2e2e2;
    border: none;
}
.cartitems .cross{
    cursor: pointer;
}
.cart-bottom{
    margin-top: 80px;
    display: flex;
    justify-content: space-between;
    gap: max(12vw, 20px);
}
.cart-total{
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 20px
    ;
}
.cart-details{
    display: flex;
    justify-content: space-between;
    color: #555;
}
.cart-total hr{
    margin: 10px 0px ;
}
.cart-total button{
    border: none;
    color: white;
    background: tomato;
    width: max(15vw,200px);
    padding: 12px 0px;
    border-radius: 4px;
    cursor: pointer;
}
.cart-promo{
    flex: 1;
}
.cart-promo p{
    color: #555;
}
.cart-input{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #eaeaea;
    border-radius: 4px;
}
.cart-input input{
    background: transparent;
    border: none;
    outline: none;
    padding-left: 10px;
}
.cart-promo button{
    width: max(10vw,150px);
    padding: 12px 5px;
    background-color: black;
    color: white;
    border-radius: 4px;
}

@media (max-width:750px){
    .cart-bottom{
        flex-direction: column-reverse;
    }
    .cart-promo{
        justify-content: start;
    }
}